	module.exports = {"data":{"postsJson":{"title":"능력, 기적과 믿음","questionsTitle":"비교, 대조와 반복","display":"block","bookName":"누가복음","ref":"7:1-8:56","html":"<p>   <strong>7</strong><u>1</u>예수께서 모든 말씀을 백성에게 들려 주시기를 마치신 후에 가버나움으로 들어가시니라 <u>2</u>어떤 백부장의 사랑하는 종이 병들어 죽게 되었더니 <u>3</u>예수의 소문을 듣고 유대인의 장로 몇사람을 예수께 보내어 오셔서 그 종을 구해 주시기를 청한지라 <u>4</u>이에 그들이 예수께 나아와 간절히 구하여 이르되</p><h1>장로들</h1><h4>이 일을 하시는 것이 이 사람에게는 합당하니이다 <u>5</u>그가 우리 민족을 사랑하고 또한 우리를 위하여 회당을 지었나이다</h4><p>하니 <u>6</u>예수께서 함께 가실새 이에 그 집이 멀지 아니하여 백부장이 벗들을 보내어 이르되</p><h1>백부장</h1><h4>주여 수고하시지 마옵소서 내 집에 들어오심을 나는 감당하지 못하겠나이다 <u>7</u>그러므로 내가 주께 나아가기도 감당하지 못할 줄을 알았나이다 말씀만 하사 내 하인을 낫게 하소서 <u>8</u>나도 남의 수하에 든 사람이요 내 아래에도 병사가 있으니 이더러 가라 하면 가고 저더러 오라 하면 오고 내 종더러 이것을 하라 하면 하나이다</h4><p><u>9</u>예수께서 들으시고 그를 놀랍게 여겨 돌이키사 따르는 무리에게 이르시되</p><h1>예수님</h1><h2>내가 너희에게 이르노니 이스라엘 중에서도 이만한 믿음은 만나보지 못하였노라:</h2><p>하시더라 <u>10</u>보내었던 사람들이 집으로 돌아가 보매 종이 이미 나아 있었더라<br />   <u>11</u>그 후에 예수께서 나인이란 성으로 가실새 제자와 많은 무리가 동행하더니 <u>12</u>성문에 가까이 이르실 때에 사람들이 한 죽은 자를 메고 나오니 이는 한 어머니의 독자요 그의 어머니는 과부라 그 성의 많은 사람도 그와 함께 나오거늘 <u>13</u>주께서 과부를 보시고 불쌍히 여기사</p><h1>예수님</h1><h2>울지 말라</h2><p>하시고 <u>14</u>가까이 가서 그 관에 손을 대시니 멘 자들이 서는지라 예수께서 이르시되</p><h1>예수님</h1><h2>청년아 내가 네게 말하노니 일어나라</h2><p>하시매 <u>15</u>죽었던 자가 일어나 앉고 말도 하거늘 예수께서 그를 어머니에게 주시니 <u>16</u>모든 사람이 두려워하며 하나님께 영광을 돌려 이르되</p><h1>나인성 사람들</h1><h4>큰 선지자가 우리 가운데 일어나셨다 하고 또 하나님께서 자기 백성을 돌보셨다</h4><p>하더라 <u>17</u>예수께 대한 이 소문이 온 유대와 사방에 두루 퍼지니라<br />   <u>18</u>요한의 제자들이 이 모든 일을 그에게 알리니 <u>19</u>요한이 그 제자 중 둘을 불러 주께 보내어 이르되</p><h1>세례 요한</h1><h4>오실 그이가 당신이오니이까 우리가 다른 이를 기다리오리이까 하라</h4><p>하매 <u>20</u>그들이 예수께 나아가 이르되</p><h1>세례 요한의 제자들</h1><h4>세례 요한이 우리를 보내어 당신께 여쭈어 보라고 하기를 오실 그이가 당신이오니이까 우리가 다른 이를 기다리오리이까 하더이다</h4><p>하니 <u>21</u>마침 그 때에 예수께서 질병과 고통과 및 악귀 들린 자를 많이 고치시며 또 많은 맹인을 보게 하신지라<br />   <u>22</u>예수께서 대답하여 이르시되</p><h1>예수님</h1><h2>너희가 가서 보고 들은 것을 요한에게 알리되 맹인이 보며 못 걷는 사람이 걸으며 나병환자가 깨끗함을 받으며 귀먹은 사람이 들으며 죽은 자가 살아나며 가난한 자에게 복음이 전파된다 하라 <u>23</u>누구든지 나로 말미암아 실족하지 아니하는 자는 복이 있도다</h2><p>하시니라<br />   <u>24</u>요한이 보낸 자가 떠난 후에 예수께서 무리에게 요한에 대하여 말씀하시되</p><h1>예수님</h1><h2>너희가 무엇을 보려고 광야에 나갔더냐 바람에 흔들리는 갈대냐 <u>25</u>그러면 너희가 무엇을 보려고 나갔더냐 부드러운 옷 입은 사람이냐 보라 화려한 옷을 입고 사치하게 지내는 자는 왕궁에 있느니라 <u>26</u>그러면 너희가 무엇을 보려고 나갔더냐 선지자냐 옳다 내가 너희에게 이르노니 선지자보다도 훌륭한 자니라 <u>27</u>기록된 바 보라<br />   내가 내 사자를 네 앞에 보내노니<br />   그가 네 앞에서 네 길을 준비하리라<br />   한 것이 이 사람에 대한 말씀이라<br />   <u>28</u>내가 너희에게 말하노니 여자가 낳은 자 중에 요한보다 큰 자가 없도다 그러나 하나님의 나라에서는 극히 작은 자라도 그보다 크니라</h2><p>하시니 <u>29</u>모든 백성과 세리들은 이미 요한의 세례를 받은지라 이 말씀을 듣고 하나님을 의롭다 하되 <u>30</u>바리새인과 율법교사들은 그의 세례를 받지 아니함으로 그들 자신을 위한 하나님의 뜻을 저버리니라<br />   <u>31</u>또 이르시되</p><h1>예수님</h1><h2>이 세대의 사람을 무엇으로 비유할까 무엇과 같은가 <u>32</u>비유하건대 아이들이 장터에 앉아 서로 불러 이르되<br />   우리가 너희를 향하여 피리를 불어도<br />   너희가 춤추지 않고<br />   우리가 곡하여도<br />   너희가 울지 아니하였다<br />   함과 같도다<br />   <u>33</u>세례 요한이 와서 떡도 먹지 아니하며 포도주도 마시지 아니하매 너희 말이 귀신이 들렸다 하더니 <u>34</u>인자는 와서 먹고 마시매 너희 말이 보라 먹기를 탐하고 포도주를 즐기는 사람이요 세리와 죄인의 친구로다 하니 <u>35</u>지혜는 자기의 모든 자녀로 인하여 옳다 함을 얻느니라</h2><p><u>36</u>한 바리새인이 예수께 자기와 함께 잡수시기를 청하니 이에 바리새인의 집에 들어가 앉으셨을 때에 <u>37</u>그 동네에 죄를 지은 한 여자가 있어 예수께서 바리새인의 집에 앉아 계심을 알고 향유 담은 옥합을 가지고 와서 <u>38</u>예수의 뒤로 그 발 곁에 서서 울며 눈물로 그 발을 적시고 자기 머리털로 닦고 그 발에 입맞추고 향유를 부으니 <u>39</u>예수를 청한 바리새인이 그것을 보고 마음에 이르되</p><h1>바리새인 시몬</h1><h4>이 사람이 만일 선지자라면 자기를 만지는 이 여자가 누구며 어떠한 자 곧 죄인인 줄을 알았으리라</h4><p>하거늘 <u>40</u>예수께서 대답하여 이르시되</p><h1>예수님</h1><h2>시몬아 내가 네게 이를 말이 있다</h2><p>하시니 그가 이르되</p><h1>바리새인 시몬</h1><h4>선생님 말씀하소서</h4><p><u>41</u>이르시되</p><h1>예수님</h1><h2>빚 주는 사람에게 빚진 자가 둘이 있어 하나는 오백 데나리온을 졌고 하나는 오십 데나리온을 졌는데 <u>42</u>갚을 것이 없으므로 둘 다 탕감하여 주었으니 둘 중에 누가 그를 더 사랑하겠느냐</h2><p><u>43</u>시몬이 대답하여 이르되</p><h1>바리새인 시몬</h1><h4>내 생각에는 많이 탕감함을 받은 자니이다</h4><p>이르시되</p><h1>예수님</h1><h2>네 판단이 옳다</h2><p>하시고 <u>44</u>그 여자를 돌아보시며 시몬에게 이르시되</p><h1>예수님</h1><h2>이 여자를 보느냐 내가 네 집에 들어올 때 너는 내게 발 씻을 물도 주지 아니하였으되 이 여자는 눈물로 내 발을 적시고 그 머리털로 닦았으며 <u>45</u>너는 내게 입맞추지 아니하였으되 그는 내가 들어올 때로부터 내 발에 입맞추기를 그치지 아니하였으며 <u>46</u>너는 내 머리에 감람유도 붓지 아니하였으되 그는 향유를 내 발에 부었느니라<br />   <u>47</u>이러므로 내가 네게 말하노니 그의 많은 죄가 사하여졌도다 이는 그의 사랑함이 많음이라 사함을 받은 일이 적은 자는 적게 사랑하느니라</h2><p><u>48</u>이에 여자에게 이르시되</p><h1>예수님</h1><h2>네 죄 사함을 받았느니라</h2><p>하시니 <u>49</u>함께 앉아 있는 자들이 속으로 말하되</p><h1>시몬의 집에 있던 사람들</h1><h4>이가 누구이기에 죄도 사하는가</h4><p>하더라<br />   <u>50</u>예수께서 여자에게 이르시되</p><h1>예수님</h1><h2>네 믿음이 너를 구원하였으니 평안히 가라</h2><p>하시니라<br />   <strong>8</strong><u>1</u>그 후에 예수께서 각 성과 마을에 두루 다니시며 하나님의 나라를 선포하시며 그 복음을 전하실새 열두 제자가 함께 하였고 <u>2</u>또한 악귀를 쫓아내심과 병 고침을 받은 어떤 여자들 곧 일곱 귀신이 나간 자 막달라인이라 하는 마리아와<u>3</u>헤롯의 청지기 구사의 아내 요안나와 수산나와 다른 여러 여자가 함께 하여 자기들의 소유로 그들을 섬기더라<br />   <u>4</u>각 동네 사람들이 예수께로 나아와 큰 무리를 이루니 예수께서 비유로 말씀하시되</p><h1>예수님</h1><h2>5씨를 뿌리는 자가 그 씨를 뿌리러 나가서 뿌릴새 더러는 길 가에 떨어지매 밟히며 공중의 새들이 먹어버렸고 <u>6</u>더러는 바위 위에 떨어지매 싹이 났다가 습기가 없으므로 말랐고 <u>7</u>더러는 가시떨기 속에 떨어지매 가시가 함께 자라서 기운을 막았고 <u>8</u>더러는 좋은 땅에 떨어지매 나서 백 배의 결실을 하였느니라 이 말씀을 하시고 외치시되 들을 귀 있는 자는 들을지어다</h2><p><u>9</u>제자들이 이 비유의 뜻을 물으니 <u>10</u>이르시되</p><h1>예수님</h1><h2>하나님 나라의 비밀을 아는 것이 너희에게는 허락되었으나 다른 사람에게는 비유로 하나니 이는<br />   그들로 보아도 보지 못하고<br />   들어도 깨닫지 못하게 하려 함이라<br />   <u>11</u>이 비유는 이러하니라 씨는 하나님의 말씀이요 <u>12</u>길 가에 있다는 것은 말씀을 들은 자니 이에 마귀가 가서 그들이 믿어 구원을 얻지 못하게 하려고 말씀을 그 마음에서 빼앗는 것이요 <u>13</u>바위 위에 있다는 것은 말씀을 들을 때에 기쁨으로 받으나 뿌리가 없어 잠깐 믿다가 시련을 당할 때에 배반하는 자요 <u>14</u>가시떨기에 떨어졌다는 것은 말씀을 들은 자이나 지내는 중 이생의 염려와 재물과 향락에 기운이 막혀 온전히 결실하지 못하는 자요 <u>15</u>좋은 땅에 있다는 것은 착하고 좋은 마음으로 말씀을 듣고 지키어 인내로 결실하는 자니라<br />   <u>16</u>누구든지 등불을 켜서 그릇으로 덮거나 평상 아래에 두지 아니하고 등경 위에 두나니 이는 들어가는 자들로 그 빛을 보게 하려 함이라 <u>17</u>숨은 것이 장차 드러나지 아니할 것이 없고 감추인 것이 장차 알려지고 나타나지 않을 것이 없느니라<br />   <u>18</u>그러므로 너희가 어떻게 들을까 스스로 삼가라 누구든지 있는 자는 받겠고 없는 자는 그 있는 줄로 아는 것까지도 빼앗기리라</h2><p>하시니라<br />   <u>19</u>예수의 어머니와 그 동생들이 왔으나 무리로 인하여 가까이 하지 못하니 <u>20</u>어떤 이가 알리되</p><h1>가버나움의 어떤 사람</h1><h4>당신의 어머니와 동생들이 당신을 보려고 밖에 서 있나이다</h4><p><u>21</u>예수께서 대답하여 이르시되</p><h1>예수님</h1><h2>내 어머니와 내 동생들은 곧 하나님의 말씀을 듣고 행하는 이 사람들이라</h2><p>하시니라<br />   <u>22</u>하루는 제자들과 함께 배에 오르사 그들에게 이르시되</p><h1>예수님</h1><h2>호수 저편으로 건너가자</h2><p>하시매 이에 떠나 <u>23</u>행선할 때에 예수께서 잠이 드셨더니 마침 광풍이 호수로 내리치매 배에 물이 가득하게 되어 위태한지라<br />   <u>24</u>제자들이 나아와 깨워 이르되</p><h1>제자들</h1><h3>주여주여우리가죽겠나이다</h3><p>한대 예수께서 잠을 깨사 바람과 물결을 꾸짖으시니 이에 그쳐 잔잔하여지더라 <u>25</u>제자들에게 이르시되</p><h1>예수님</h1><h2>너희 믿음이 어디 있느냐</h2><p>하시니 그들이 두려워하고 놀랍게 여겨 서로 말하되</p><h1>제자들</h1><h3>그가누구이기에바람과물을명하매순종하는가</h3><p>하더라<br />   <u>26</u>그들이 갈릴리 맞은편 거라사인의 땅에 이르러 <u>27</u>예수께서 육지에 내리시매 그 도시 사람으로서 귀신 들린 자 하나가 예수를 만나니 그 사람은 오래 옷을 입지 아니하며 집에 거하지도 아니하고 무덤 사이에 거하는 자라<br />   <u>28</u>예수를 보고 부르짖으며 그 앞에 엎드려 큰 소리로 불러 이르되</p><h1>군대 귀신들린 사람</h1><h4>지극히 높으신 하나님의 아들 예수여 당신이 나와 무슨 상관이 있나이까 당신께 구하노니 나를 괴롭게 하지 마옵소서</h4><p>하니 <u>29</u>이는 예수께서 이미 더러운 귀신을 명하사 그사람에게서 나오라 하셨음이라 (귀신이 가끔 그 사람을 붙잡으므로 그를 쇠사슬과 고랑에 매어 지켰으되 그 맨것을 끊고 귀신에게 몰려 광야로 나갔더라)<br />   <u>30</u>예수께서</p><h1>예수님</h1><h2>네 이름이 무엇이냐</h2><p>물으신즉 이르되</p><h1>군대 귀신들린 사람</h1><h4>군대라</h4><p>하니 이는 많은 귀신이 들렸음이라 <u>31</u>무저갱으로 들어가라 하지 마시기를 간구하더니 <u>32</u>마침 그 곳에 많은 돼지 떼가 산에서 먹고 있는지라 귀신들이 그 돼지에게로 들어가게 허락하심을 간구하니 이에 허락하시니 <u>33</u>귀신들이 그 사람에게서 나와 돼지에게로 들어가니 그 떼가 비탈로 내리달아 호수에 들어가 몰사하거늘 <u>34</u>치던 자들이 그 이루어진 일을 보고 도망하여 성내와 마을에 알리니 <u>35</u>사람들이 그 이루어진 일을 보러 나와서 예수께 이르러 귀신 나간 사람이 옷을 입고 정신이 온전하여 예수의 발치에 앉아 있는 것을 보고 두려워하거늘 <u>36</u>귀신 들렸던 자가 어떻게 구원 받았는지를 본 자들이 그들에게 이르매 <u>37</u>거라사인의 땅 근방 모든 백성이 크게 두려워하여 예수께 떠나가시기를 구하더라<br />   예수께서 배에 올라 돌아가실새 <u>38</u>귀신 나간 사람이 함께 있기를 구하였으나 예수께서 그를 보내시며 이르시되</p><h1>예수님</h1><h2>39집으로 돌아가 하나님이 네게 어떻게 큰 일을 행하셨는지를 말하라</h2><p>하시니 그가 가서 예수께서 자기에게 어떻게 큰 일을 행하셨는지를 온 성내에 전파하니라<br />   <u>40</u>예수께서 돌아오시매 무리가 환영하니 이는 다 기다렸음이러라<u>41</u>이에 회당장인 야이로라 하는 사람이 와서 예수의 발 아래에 엎드려 자기 집에 오시기를 간구하니 <u>42</u>이는 자기에게 열두 살 된 외딸이 있어 죽어감이러라<br />   예수께서 가실 때에 무리가 밀려들더라 <u>43</u>이에 열두 해를 혈루증으로 앓는 중에 아무에게도 고침을 받지 못하던 여자가 <u>44</u>예수의 뒤로 와서 그의 옷 가에 손을 대니 혈루증이 즉시 그쳤더라 <u>45</u>예수께서 이르시되</p><h1>예수님</h1><h2>내게 손을 댄 자가 누구냐</h2><p>하시니 다 아니라 할 때에 베드로가 이르되</p><h1>시몬 베드로</h1><h3>주여무리가밀려들어미나이다</h3><p><u>46</u>예수께서 이르시되</p><h1>예수님</h1><h2>내게 손을 댄 자가 있도다 이는 내게서 능력이 나간 줄 앎이로다</h2><p>하신대 <u>47</u>여자가 스스로 숨기지 못할 줄 알고 떨며 나아와 엎드리어 그 손 댄 이유와 곧 나은 것을 모든 사람 앞에서 말하니 <u>48</u>예수께서 이르시되</p><h1>예수님</h1><h2>딸아 네 믿음이 너를 구원하였으니 평안히 가라</h2><p>하시더라<br />   <u>49</u>아직 말씀하실 때에 회당장의 집에서 사람이 와서 말하되</p><h1>회당장의 사람</h1><h4>당신의 딸이 죽었나이다 선생님을 더 괴롭게 하지 마소서</h4><p>하거늘 <u>50</u>예수께서 들으시고 이르시되</p><h1>예수님</h1><h2>두려워하지 말고 믿기만 하라 그리하면 딸이 구원을 얻으리라</h2><p>하시고 <u>51</u>그 집에 이르러 베드로와 요한과 야고보와 아이의 부모 외에는 함께 들어가기를 허락하지 아니하시니라 <u>52</u>모든 사람이 아이를 위하여 울며 통곡하매 예수께서 이르시되</p><h1>예수님</h1><h2>울지 말라 죽은 것이 아니라 잔다</h2><p>하시니 <u>53</u>그들이 그 죽은 것을 아는 고로 비웃더라 <u>54</u>예수께서 아이의 손을 잡고 불러 이르시되</p><h1>예수님</h1><h2>아이야 일어나라</h2><p>하시니 <u>55</u>그 영이 돌아와 아이가 곧 일어나거늘 예수께서 먹을 것을 주라 명하시니 <u>56</u>그 부모가 놀라는지라 예수께서 경고하사 이 일을 아무에게도 말하지 말라 하시니라</p>","path":"/329","Question1":"비교란 둘 혹은 셋 이상의 비슷한 사람들, 아이디어들과 상황들을 연상하는 것입니다. 이 이야기의 저자가 한 비교들을 발견할 수 있습니까? 왜 당신은 저자가 이런 비교를 했다고 생각합니까?","Question2":"구별된 두 세사람, 아이디어들 혹은 상황들을 옆에 놓고 비교해 보십시오. 이 이야기의 저자에 의해 비교된 것이 있는지 발견할 수 있습니까? 저자는 왜 이런 대조를 했다고 생각하십니까?","Question3":"시간을 가지고 이 이야기속에서 반복되는 단어나 개념을 주목해보십시오. ","Question4":"이 비교, 대조, 반복들을 통해 발견한 것들을 가지고 하나님께 반응한 것을 간단한 기도문으로 작성하십시오.","prevPath":"/328","nextPath":"/330"}},"pathContext":{}}

